<template>
	<div id="takeout-list">
		<public-header title="订单列表"></public-header>
		<public-footer :showPreLoading="showPreLoading" :menufooter="menufooter"></public-footer>
		<div class="content">
			<van-tabs
				v-model="tabActive"
				@change="onChangeStatus"
				color="#0EC3B3"
				title-active-color="#0EC3B3"
				title-inactive-color="#000"
				:line-width="80"
				:line-height="2"
				:offset-top="46"
				sticky
				swipeable
			>
				<van-tab v-for="(item, index) in tabs" :key="index">
					<div slot="title" class="tab-title">
						{{item.status_cn}} <span class="order-num" v-if="item.num > 0">{{item.num}}</span>
					</div>
					<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
						<van-list
								v-model="records.loading"
								:finished="records.finished"
								@load="onLoad"
								:offset="100"
								:immediate-check="false"
								v-if="!records.empty"
						>
							<div class="order-item" :class="{reserve: item.is_reserve == 1}" v-for="(item, index) in records.data" :key="item.id">
								<div class="van-hairline--bottom flex-lr padding-15-tb padding-10-lr" @click="onDetail(item.id)">
									<div class="flex">
										<span class="c-primary font-500 font-18 order-sn">#{{item.serial_sn}}</span>
										<div class="distance flex">
											<div class="img-wrap">
												<img class="w-100" src="static/img/start.png" alt="">
											</div>
											<div class="text">-{{item.store2deliveryer_distance}}km-</div>
											<div class="img-wrap">
												<img class="w-100" src="static/img/ds.png" alt="">
											</div>
											<div class="text">-{{item.store2user_distance}}km-</div>
											<div class="img-wrap">
												<img class="w-100" src="static/img/end.png" alt="">
											</div>
										</div>
									</div>
									<span class="c-danger font-15 font-500">¥ {{item.plateform_deliveryer_fee}}</span>
								</div>
								<div class="padding-10-lr margin-15-t">
									<div class="flex-lr">
										<ul class="order-tags">
											<li class="handle-type" v-if="item.status != 3">{{item.delivery_collect_type_cn}}</li>
											<li v-if="0">保温服务</li>
										</ul>
										<div class="c-danger flex" v-if="item.status == 4 && item.delivery_status != 3">
											<template v-if="item.delivery_overtime > 0">
												距超时<count-down :endTime="item.delivery_overtime" class="c-danger"></count-down>
											</template>
											<template v-else-if="item.delivery_overtime_start > 0">
												已超时<count-down :startTime="item.delivery_overtime_start" class="c-danger"></count-down>
											</template>
										</div>
									</div>
								</div>
								<ul class="address padding-10-lr" @click="onDetail(item.id)">
									<li class="flex-lr margin-15-t">
										<span class="c-info font-15">取:</span>
										<ul class="address-text">
											<li class="font-15 font-500">{{item.store.title}}</li>
											<li class="c-gray margin-5-t">{{item.store.address}}</li>
										</ul>
									</li>
									<li class="flex-lr margin-15-t">
										<span class="c-primary font-15">送:</span>
										<ul class="address-text">
											<li class="font-15 font-500 ">{{item.address}}</li>
										</ul>
									</li>
								</ul>
								<div class="note" v-if="item.note">
									<div class="note-title">备注:</div>
									<div class="note-text">{{item.note}}</div>
								</div>
								<ul class="time padding-10-lr margin-15-t flex-lr">
									<li>{{item.addtime_cn}}下单</li>
									<li>配送 {{item.delivery_day}} {{item.delivery_time}}</li>
								</ul>
								<ul class="btn-group margin-15-t padding-10-lr flex-lr">
									<template v-if="item.delivery_status == 3">
										<li class="btn-item bg-warning" @click="onChangeOrderStatus(index, 'delivery_assign')">抢单</li>
									</template>
									<template v-if="item.delivery_status == 4 || item.delivery_status == 7 || item.delivery_status == 8">
										<template v-if="item.transfer_delivery_status == 1">
											<li class="btn-item bg-danger" @click="onChangeOrderStatus(index, 'direct_transfer_refuse')">拒绝转单</li>
											<li class="btn-item bg-primary" @click="onChangeOrderStatus(index, 'direct_transfer_agree')">接受转单</li>
										</template>
										<template v-else>
											<template v-if="item.delivery_status == 4">
												<li class="btn-item bg-warning" v-if="item.mobile" @click="onCallCustomer(item)">联系顾客</li>
												<li class="btn-item bg-primary" @click="onChangeOrderStatus(index, 'delivery_success')">确认送达</li>
											</template>
											<template v-else>
												<li class="btn-item" v-if="item.store && item.store.telephone" @click="util.jsTel(item.store.telephone)">联系商家</li>
												<li class="btn-item bg-primary" @click="onChangeOrderStatus(index, 'delivery_takegoods')">确认取货</li>
											</template>
										</template>
									</template>
								</ul>
								<div v-if="item.is_pay == 1">
									<div class="have-pay" v-if="item.pay_type != 'delivery'"></div>
									<div class="delivery-pay" v-else-if="item.pay_type == 'delivery'"></div>
								</div>
							</div>
						</van-list>
						<div class="no-data" v-else>
							<img src="static/img/order_no_con.png" alt="">
							<template v-if="filter.items.status == 3">
								<template v-if="deliveryer.work_status == 1">
									<template v-if="deliveryer.perm_takeout > 0">
										<template v-if="!can_collect_order">
											<p >当前调度模式不允许抢单,请等待管理员或系统派单</p>
										</template>
										<template v-else>
											<p>暂无待抢的订单</p>
										</template>
									</template>
									<template v-else>
										<p>您没有外卖配送权限，请联系管理员</p>
									</template>
								</template>
								<template v-else>
									<p>您当前处于收工状态,不能抢单</p>
								</template>
							</template>
							<template v-else>
								<p>暂无符合条件的订单</p>
							</template>
						</div>
					</van-pull-refresh>
				</van-tab>
			</van-tabs>
			<failed-tips :failedTips="failedTips" v-if="failedTips.show"></failed-tips>
		</div>
		<van-dialog
			v-model="yinsihao.status"
			show-cancel-button
			title="顾客号码保护中"
			confirm-button-text="立即联系"
			@confirm="util.jsTel(yinsihao.secret_mobile)"
		>
			<div class="padding-15-tb text-center font-16">
				<span class="font-bold c-default">{{yinsihao.secret_mobile}}</span>
				<span class="padding-10-lr">转</span>
				<span class="font-bold c-danger">{{yinsihao.extension}}</span>
			</div>
			<div class="padding-15-b font-14 c-gray text-center">播打{{yinsihao.secret_mobile}}接通后，输入{{yinsihao.extension}}#即可</div>
		</van-dialog>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import publicFooter from "@/components/footer"
	import countDown from "@/components/countDown"
	import {getRecords, getData} from "../../controller/funcCommon"
	import {changeOrderStatus} from "./controller/order.js"
	export default {
		components: {
			publicHeader,
			publicFooter,
			countDown
		},
		data() {
			return {
				tabActive: 0,
				tabs: [
					{
						status: 3,
						status_cn: '待抢',
						num: 0,
					},
					{
						status: 7,
						status_cn: '待取货',
						num: 0,
					},
					{
						status: 4,
						status_cn: '配送中',
						num: 0,
					},
					{
						status: 5,
						status_cn: '已完成',
						num: 0,
					}
				],
				filter: {
					items: {
						status: 3
					}
				},
				isRefresh: false,
				showPreLoading: true,
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				deliveryer: {},
				can_collect_order: false,
				config: {},
				yinsihao: {
					status: false,
					secret_mobile: '',
					extension: ''
				},
				failedTips: {
					show: false,
					type: 'message',
					tips: '您没有外卖单的配送权限，请联系平台管理员',
					btnText: '',
					link: '',
					position: 'absolute',
				},
				menufooter: {},
			}
		},
		methods: {
			onChangeStatus(index, title) {
				var that = this;
				that.filter.items.status = that.tabs[index]['status'];
				this.onLoad(true);
			},
			onLoad(force){
				var that = this;
				getRecords({
					vue: that,
					force: force,
					url: 'delivery/order/takeout/list',
					recordsName: 'orders',
					data: {
						menufooter: 1,
						_deliveryerOrderTabType: 'takeout',
					},
					success: function(result) {
						that.deliveryer = result.deliveryer;
						that.can_collect_order = result.can_collect_order;
						that.config = result.config;
					},
					fail: function() {
						that.failedTips.show = true;
					}
				});
				if(that.util.isDlala()) {
					window.apiready = function() {
						that.util.startLocation({
							vue: that
						});
					};
				}
			},

			onCallCustomer(order) {
				var that = this;
				if(order.data && order.data.yinsihao_status == 1) {
					getData({
						vue: that,
						url: 'yinsihao/yinsihao',
						data: {
							order_id: order.id,
							type: 'member',
							ordersn: order.ordersn
						},
						success: function(res) {
							that.yinsihao.secret_mobile = res.data.secret_mobile;
							that.yinsihao.extension = res.data.extension;
							that.yinsihao.status = true;
						}
					})
				} else {
					that.util.jsTel(order.mobile)
				}
			},

			onChangeOrderStatus(index, type) {
				var that = this;
				changeOrderStatus({
					vue: that,
					type: type,
					index: index,
					from: 'list',
					order: that.records.data[index],
				})
			},

			onDetail(id) {
				if(this.filter.items.status == 3) {
					this.util.$toast('抢单后才能查看详情', '', 1000);
					return;
				}
				this.$router.push(this.util.getUrl({path: 'pages/order/detail', query: {id: id}}));
			},

			onPullDownRefresh() {
				this.onLoad(true);
			},
		},
		created() {
			this.query = this.$route.query;
			if(this.query && this.query.status) {
				this.filter.items.status = this.query.status;
			}
		},
		mounted() {
			var that = this;
			if(!that.util.getStorage('idtoken') && that.util.isDlala()) {
				window.apiready = function() {
					var idtoken = api.getPrefs({
						sync: true,
						key: 'idtoken'
					});
					if(idtoken) {
						that.util.setStorage('idtoken', idtoken);
					}
					that.onLoad();
					setInterval(function() {
						if(that.filter.items.status == 3 && that.config.auto_refresh == 1) {
							that.onLoad(true);
						}
					}, 15000);
				};
			} else {
				that.onLoad();
				setInterval(function() {
					if(that.filter.items.status == 3 && that.config.auto_refresh == 1) {
						that.onLoad(true);
					}
				}, 15000);
			}
			this.util.imap();
			this.util.icloudapi({
				vue: that,
				page: 'orderTakeout'
			});
		}
	}
</script>

<style>
	#takeout-list .tab-title{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: center;
	}
	#takeout-list .tab-title .order-num{
		display: inline-flex;
		align-items: center;
		justify-content: center;
		min-width: 14px;
		height: 14px;
		padding: 0 5px;
		margin-left: 3px;
		border-radius: 14px;
		color: #fff;
		background-color: #0EC3B3;
		font-size: 12px;
	}
	#takeout-list .order-item{
		position: relative;
		background-color: #fff;
		font-size: 14px;
		margin-top: 10px;
		padding-bottom: 15px;
	}
	#takeout-list .order-item:first-child{
		margin-top: 0px;
	}
	#takeout-list .order-item .distance{
		margin-left: 20px;
	}
	#takeout-list .order-item .distance .img-wrap{
		width: 25px;
		font-size: 0px;
	}
	#takeout-list .order-item .order-tags{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		font-size: 12px;
	}
	#takeout-list .order-item .order-tags li{
		padding: 3px 5px;
		border-radius: 3px;
		margin-right: 5px;
		color: #FF2D4B;
		border: 1px solid #FF2D4B;
	}
	#takeout-list .order-item .order-tags .handle-type{
		background-color: #FF2D4B;
		color: #fff;
		font-style: italic;
		padding: 3px 7px 3px 3px;
	}
	#takeout-list .order-item .address-text{
		flex: 1;
		padding-left: 15px;
	}
	#takeout-list .order-item .note{
		margin: 15px 10px 0 10px;
		padding: 10px;
		border-radius: 3px;
		background-color: #DFFBF9;
		color: #16C5B6;
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
	}
	#takeout-list .order-item .note .note-text{
		flex: 1;
		padding-left: 10px;
		line-height: 1.2;
	}
	#takeout-list .order-item .time li{
		color: #1AB5F5;
		border: 1px solid #1AB5F5;
		font-size: 12px;
		padding: 2px 5px;
		border-radius: 3px;
	}
	#takeout-list .order-item .btn-group .btn-item{
		flex: 1;
		height: 44px;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 5px;
		font-size: 15px;
		color: #fff;
		background-color: #189FD1;
	}
	#takeout-list .order-item .btn-group .btn-item:nth-child(2){
		margin-left: 10px;
	}
	#takeout-list .order-item .have-pay{
		position: absolute;
		width: 100px;
		height: 50px;
		left: 50%;
		margin-left: -50px;
		top: 100px;
		background: url("http://1.duoxunwl.com/addons/we7_wmall/static/img/have-pay.png") no-repeat center center;
		background-size: 100% 100%;
	}
	#takeout-list .order-item .delivery-pay{
		position: absolute;
		width: 110px;
		height: 70px;
		left: 50%;
		margin-left: -50px;
		top: 100px;
		background: url("http://1.duoxunwl.com/addons/we7_wmall/static/img/delivery-pay.png") no-repeat center center;
		background-size: 100% 100%;
	}
	/*不同渠道订单颜色区分*/
	#takeout-list .order-item.meituan .order-sn{
		color: #FFB814;
	}
	#takeout-list .order-item.meituan .order-sn:after{
		content: '美团';
		font-size: 16px;
		font-weight: bold;
		color: #FFB814;
		margin-left: 5px;
	}
	#takeout-list .order-item.eleme .order-sn{
		color: #2395ff;
	}
	#takeout-list .order-item.eleme .order-sn:after{
		content: '饿了么';
		font-size: 16px;
		font-weight: bold;
		color: #2395ff;
		margin-left: 5px;
	}
	#takeout-list .order-item.reserve .order-sn{
		color: #ff8c26;
	}
	#takeout-list .order-item.reserve .order-sn:after{
		content: '预定';
		font-size: 15px;
		font-weight: bold;
		color: #ff8c26;
		margin-left: 5px;
	}
	#takeout-list .van-dialog .van-dialog__confirm{
		color: #0EC3B3;
	}
</style>


